import lazy from "react-lazy-with-preload";
import { RouteDetails } from "../../types";
import FlakyUITestComponent from "../Projects/FlakyUITests";
// import HomeComponent from '../Home';
// import AboutComponent from './About';

const HomeComponent = lazy(() => import("../Home"));
const ResumeComponent = lazy(() => import("../Resume"));
const ProjectsComponent = lazy(() => import("../Projects"));
const WasmView = lazy(() => import("../Projects/WasmView"));
const WASim = lazy(() => import("../Projects/WASim"));
const MinerRay = lazy(() => import("../Projects/MinerRay"));
const WebAssemblyCompilerBugs = lazy(
  () => import("../Projects/WebAssemblyCompilerBugs")
);
const ToolsComponent = lazy(() => import("../Tools"));
const NewLineComponent = lazy(() => import("../Tools/NewLineApp/NewLineApp"));
const SetDiffComponent = lazy(
  () => import("../Tools/SetDifferenceApp/SetDifferenceApp")
);
const WebAssemblyDecoderComponent = lazy(
  () => import("../Tools/WebAssemblyDecoder/WebAssemblyDecoding")
);

const AppRoutes: RouteDetails[] = [
  {
    route: "/",
    showOnNav: true,
    exact: true,
    title: "Home",
    sublinks: [],
    component: HomeComponent,
  },
  {
    route: "/resume.html",
    showOnNav: true,
    exact: false,
    title: "Resume",
    sublinks: [],
    component: ResumeComponent,
  },
  {
    route: "/projects.html",
    showOnNav: true,
    exact: false,
    title: "Projects",
    sublinks: [
      { title: "WebAssembly Compiler Bug Study", link: "/compiler-bugs.html" },
      { title: "Flaky UI Tests", link: "/flakyui.html" },
      { title: "MinerRay", link: "/minerray.html" },
      { title: "WASim", link: "/wasim.html" },
      { title: "WasmView", link: "/wasmview.html" },
    ],
    component: ProjectsComponent,
  },
  {
    route: "/tools.html",
    showOnNav: true,
    exact: false,
    title: "Tools",
    sublinks: [
      { title: "WebAssembly Decoder", link: "/wasmdecoder.html" },
      { title: "Set Diff", link: "/setdiff.html" },
      { title: "Newline Escaping ", link: "/newline.html" },
    ],
    component: ToolsComponent,
  },
  // {
  //     route: '/about.html',
  //     showOnNav: true,
  //     title:'About',
  //     sublinks: [],
  //     component: AboutComponent
  // },
  {
    route: "/setdiff.html",
    showOnNav: false,
    exact: false,
    title: "Set Difference App",
    sublinks: [],
    component: SetDiffComponent,
  },
  {
    route: "/newline.html",
    showOnNav: false,
    exact: false,
    title: "New Line App",
    sublinks: [],
    component: NewLineComponent,
  },
  {
    route: "/wasmview.html",
    showOnNav: false,
    exact: false,
    title: "WasmView",
    sublinks: [],
    component: WasmView,
  },
  {
    route: "/wasim.html",
    showOnNav: false,
    exact: false,
    title: "WASim",
    sublinks: [],
    component: WASim,
  },
  {
    route: "/minerray.html",
    showOnNav: false,
    exact: false,
    title: "MinerRay",
    sublinks: [],
    component: MinerRay,
  },
  {
    route: "/flakyui.html",
    showOnNav: false,
    exact: false,
    title: "Flaky UI Tests",
    sublinks: [],
    component: FlakyUITestComponent,
  },
  {
    route: "/compiler-bugs.html",
    showOnNav: false,
    exact: false,
    title: "WebAssembly Compiler Bug Study",
    sublinks: [],
    component: WebAssemblyCompilerBugs,
  },
  {
    route: "/wasmdecoder.html",
    showOnNav: false,
    exact: false,
    title: "WebAssembly Decoder",
    sublinks: [],
    component: WebAssemblyDecoderComponent,
  },
];

HomeComponent.preload()
  .then(() => ResumeComponent.preload())
  .then(() => ProjectsComponent.preload())
  .then(() => WasmView.preload())
  .then(() => WebAssemblyCompilerBugs.preload())
  .then(() => WASim.preload())
  .then(() => MinerRay.preload())
  .then(() => ToolsComponent.preload())
  .then(() => NewLineComponent.preload())
  .then(() => SetDiffComponent.preload())
  .then(() => ProjectsComponent.preload());

export default AppRoutes;
